import ConnectButton from "./ConnectButton";
import constants from "../params";

//Mint new okto
export default function MintButton(params) {
    //Mint new octo nft
    async function mint() {
        let openMint = await params.aquarium.openMint();
        if(!openMint) {
            await mintWhitelist();
        } else {
            let gen0 = await params.oktoNFT.currentGen() == 0;
            if(gen0) await mintGen0();
            else await mintGenX();
        }
        console.log("calling back")
        setTimeout(params.afterMint, 20000);
    }
    //Mint as whitelisted address
    async function mintWhitelist() {
        await params.aquarium.mintWhitelist(constants.mintCost, params.mintNum);
    }
    //Mint from gen0
    async function mintGen0() {
        await params.aquarium.mintGen0(constants.mintCost, params.mintNum);
    }
    //Mint from gens 1-3
    async function mintGenX() {
        await params.aquarium.mintGenX(params.mintNum);
    }
    if(!params.oktoNFT.connectionManager.connected) {
        return(
            <ConnectButton 
                connectionManager={params.oktoNFT.connectionManager} 
                setConnected = {params.setConnected} 
            />
        );
    }
    return (
        <div className="customButtonLink" onClick={mint} >
            <div className="customButton" >
                <div className="customButtonContainer"><span>Mint now</span></div>
            </div>
        </div>
    );
}