import Fade from 'react-fade-in';
import React from "react";

import utils from "../utils";
import * as ethers from "ethers";

import './vault.css';

import LoadingDiv from "../global/LoadingDiv";
import ConnectDiv from "../global/ConnectDiv";


export default function VaultPage(params) {
     let account = params.vault.connectionManager.account;
     if(account == undefined) {
         return (
            <div className='connect'>
                <ConnectDiv 
                    connectionManager={params.vault.connectionManager} 
                    setConnected={params.setConnected} 
                    connector={params.connector}
                />
            </div>
         );
     }
     let [lastState, setState] = React.useState({
         deposits: -1,
         backing: -1,
         dailyRewards: -1,
         vaultBalance: -1,
         oktoBalance: -1,
         unclaimedFTM: -1
     });
     let state = {
         deposits: lastState.deposits,
         backing: lastState.backing,
         rewards: lastState.rewards,
         dailyRewards: lastState.dailyRewards,
         vaultBalance: lastState.vaultBalance,
         oktoBalance: lastState.oktoBalance,
         unclaimedFTM: lastState.unclaimedFTM
     }
     React.useEffect(() => {
         loadPage();
     }, []); 
     //load token info
     async function loadPage() {
        console.log("loading page");
        state.deposits = await params.vault.deposits();
        state.backing = await params.vault.backing();
        state.dailyRewards = await params.vault.dailyRewards();
        state.vaultBalance = await params.vault.balanceOf(account);
        state.oktoBalance = await params.oktoCoin.balanceOf(account);
        state.unclaimedFTM = await params.vault.rewards(account);
        setState(state);
     }
     //Approve transfer to vault if not already approved
     async function confirmApproval() {
         let approvalAmount = await params.oktoCoin.allowance(account, params.vault.address);
         approvalAmount = ethers.BigNumber.from(approvalAmount);
         console.log(approvalAmount, state.oktoBalance);
         if(approvalAmount.lt(state.oktoBalance)) {
             console.log("approving");
             await params.oktoCoin.approve(params.vault.address, utils.oo);
         }
         console.log("approval confirmed");
     }
     //Deposit to vault
     async function deposit() {
        await confirmApproval();
        params.vault.deposit(state.oktoBalance);
        setTimeout(loadPage, 20000);
     }
     //Withdraw
     async function withdraw() {
         await params.vault.withdraw(state.vaultBalance);
         setTimeout(loadPage, 20000);
     }
     //Collect rewards
     async function claim() {
         await params.vault.claim();
         setTimeout(loadPage, 20000);
     }

     if(state.deposits == -1) return <LoadingDiv />;

    return (
        <Fade>
            <div className="vault-app">   
                <div className="vault-container">
                    <div className="pixelBoxed">
                        <span>
                            <div className="pixelBoxedTentacles1">
                                <div className="pixelBoxedTentacles2">
                                    <div className="pixelBoxedContainer">

                                        <p className="text-title"> Vault </p>
                                        <p className="text-description"> Stake $FOOD and earn $FTM </p>

                                        <div className="vault">

                                            <div className="vault-left">

                                            <div className="vault-text-container">
                                                    <p className="vault-text-text purple"> Total deposits : </p>
                                                    <p className="vault-text-text"> {utils.decimalize(state.deposits.toString())} </p> 
                                                </div>

                                                <div className="vault-text-container">
                                                    <p className="vault-text-text purple"> Total backing : </p>
                                                    <p className="vault-text-text"> {utils.decimalize(state.backing.toString())} </p> 
                                                </div>

                                                <div className="vault-text-container">
                                                    <p className="vault-text-text purple">Daily FTM rewards per 10k $FOOD : </p>
                                                    <p className="vault-text-text">{utils.decimalize(state.dailyRewards.toString(), 6)}</p>
                                                </div>

                                                <div className="vault-text-container">
                                                    <p className="vault-text-text purple"> Your deposited $FOOD : </p>
                                                    <p className="vault-text-text"> {utils.decimalize(state.vaultBalance.toString(), 6)} </p>
                                                </div>

                                                <div className="vault-text-container">
                                                    <p className="vault-text-text purple"> Your unclaimed FTM : </p>
                                                    <p className="vault-text-text"> {utils.decimalize(state.unclaimedFTM.toString())} </p>
                                                </div>

                                                <div className="vault-text-container">
                                                    <p className="vault-text-text purple"> Your $FOOD balance : </p>
                                                    <p className="vault-text-text"> {utils.decimalize(state.oktoBalance.toString())} </p>
                                                </div>

                                            </div>

                                            <div className="vault-right">

                                                <div className="customButtonLink" onClick={deposit}>
                                                    <div className="customButton">
                                                        <div className="customButtonContainer"><span>Deposit</span></div>
                                                    </div>
                                                </div>

                                                <div className="customButtonLink" onClick={claim} style={{paddingTop: '1vw'}}>
                                                    <div className="customButton">
                                                        <div className="customButtonContainer"><span>Claim</span></div>
                                                    </div>
                                                </div>

                                                <div className="customButtonLink" onClick={withdraw} style={{paddingTop: '1vw'}}>
                                                    <div className="customButton">
                                                        <div className="customButtonContainer"><span>Withdraw</span></div>
                                                    </div>
                                                </div>

                                            </div>
                                        
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </span>
                    </div>
                </div>
            </div>
        </Fade>
    );
}