import './App.css';
import React from 'react';
import { BrowserRouter as BrowserRouter, Route, Routes } from "react-router-dom";

import WalletConnect from "@walletconnect/client";
import QRCodeModal from "@walletconnect/qrcode-modal";

import AquariumPage from "./routes/Aquarium";
import LandingPage from "./routes/Landing";
import SpawnPage from "./routes/Spawn";
import VaultPage from "./routes/Vault";
import NavBar from "./global/NavBar";

import Footer from "./global/Footer";

import ConnectionManager from "./objects/ConnectionManager";
import Aquarium from "./objects/Contracts/Aquarium";
import OktoCoin from "./objects/Contracts/OktoCoin";
import OktoNFT from "./objects/Contracts/OktoNFT";
import Vault from "./objects/Contracts/Vault";

import aquariumAbi from "./abi/Aquarium.json";
import oktoCoinAbi from "./abi/OktoCoin.json";
import oktoNFTAbi from "./abi/OktoNFT.json";
import vaultAbi from "./abi/Vault.json";

import params from "./params.js";

import listBubbles from "./ui/listBubbles";
import LoadingDiv from "./global/LoadingDiv";


export default function App() {
  //Walletconnect instance
  let connector = new WalletConnect({
    bridge: "https://bridge.walletconnect.org",
    qrcodeModal: QRCodeModal,
  });
  if(window.ethereum != undefined) connector = undefined;
  //State to store between calls
  let [lastState, setState] = React.useState({
    status: {
      connected: false
    },
    connectionManager: undefined,
    contracts: {
      aquarium: undefined,
      oktoNFT: undefined,
      oktoCoin: undefined,
      vault: undefined
    },
    init: false
  });
  //current state
  let state = {
    status: {
      connected: lastState.status.connected
    },
    connectionManager: lastState.connectionManager,
    contracts: {
      aquarium: lastState.contracts.aquarium,
      oktoNFT: lastState.contracts.oktoNFT,
      oktoCoin: lastState.contracts.oktoCoin,
      vault: lastState.contracts.vault
    },
    init: lastState.init
  }
  //Once on load
  React.useEffect(() => {
    document.title = "Okto";
    localStorage.clear();
    state.connectionManager = new ConnectionManager();
    initializeContracts();
    console.log("set connection manager");
    state.init = true;
    setState(state);
  }, []);
  
  //initialize contracts with params from params file
  function initializeContracts() {
    state.contracts.aquarium = new Aquarium(aquariumAbi, params.aquariumAddress, state.connectionManager);
    state.contracts.oktoNFT = new OktoNFT(oktoNFTAbi, params.oktoNFTAddress, state.connectionManager);
    state.contracts.oktoCoin = new OktoCoin(oktoCoinAbi, params.oktoCoinAddress, state.connectionManager);
    state.contracts.vault = new Vault(vaultAbi, params.vaultAddress, state.connectionManager);
  }
  //Called by navbar when connectionManager is initialized
  function setConnected() {
    state.status.connected = true;
    console.log("connected")
    setState(state);
  }
  if(!state.init) return <LoadingDiv />;
  //Render
  return (
    <div className="App">
      <BrowserRouter>
        <NavBar 
          connector={connector} 
          connectionManager={state.connectionManager} 
          setConnected={setConnected}
        />      
        <Routes>
          <Route path="/" exact element={<LandingPage connector={connector} />} />
          <Route path="/spawn" exact element={
            <SpawnPage 
              connector={connector} 
              oktoNFT={state.contracts.oktoNFT}
              aquarium={state.contracts.aquarium}
              setConnected={setConnected}
            /> 
          } />
          <Route path="/aquarium" exact element={
            <AquariumPage 
              connector={connector} 
              oktoNFT={state.contracts.oktoNFT}
              aquarium={state.contracts.aquarium}
              setConnected={setConnected}
            />
          } />
          <Route path="/vault" exact element={
            <VaultPage 
              connector={connector} 
              oktoCoin={state.contracts.oktoCoin}
              vault={state.contracts.vault}
              setConnected={setConnected}
            />
          } />
        </Routes>
        
        <Footer />
      </BrowserRouter>

      {listBubbles.map((list, idx) => (
        <img alt="" key={idx}className="bubbles" src={list.bubble} style={{width: list.width, height: list.height, left: list.left, animationDuration: list.duration }}/>
      ))}

    </div>
  );
}