import ContractWriter from "./ContractWriter.jsx";
import View from "./View.jsx";

export default class Contract {
    constructor(abi, address, connectionManager) {
        this.abi = abi;
        this.address = address;
        this.connectionManager = connectionManager;
        connectionManager.addContract(this);//Add to manager to initialize writer when manager is initializes
        this.view = new View(abi, address, connectionManager);
    }
    initWriter() {
        this.writer = new ContractWriter(this.abi, this.address, this.connectionManager);
    }
}