
//Used for interacting with contracts
export default class ContractWriter {
    constructor(abi, address, connectionManager) {
        this.abi = abi;
        this.address = address;
        this.connectionManager = connectionManager;
        this.methods = this.connectionManager.methods(this.abi, this.address);
    }
    async send(call, args) {
        if(args == undefined) args = {};
        if(args.to == undefined) args.to = this.address;
        await this.connectionManager.send(call, args);
    } 
}