import React, { useState } from 'react';
import ConnectButton from "../components/ConnectButton.jsx";
import { Link, useLocation } from "react-router-dom";
import useWindowDimensions from '../ui/useWindowDimensions';
import NavBarMobile from './NavBarMobile';

import Logo from "../assets/images/okto.png";

const url = "http://localhost:19006"

//Top nav bar. Used to connect wallet
export default function NavBar(params) {

    const location = useLocation();

    // Set navbar for mobile
    const { height, width } = useWindowDimensions();
    const mobileScreen = (width <= 1000);

    // Toggle menu
    const [toggleMenu, setToggle] = useState(false)

    const toggleNavbar = () => {
        setToggle(currentValue => !currentValue);
    }

    return (
        <div id="navbar">

            { !mobileScreen &&
                <div className="pixelBoxed" style={{width: '100%'}}>
                    <span>
                        <div className="pixelBoxedContainer">
                            <Link className="headLogo" to="/" >
                                <img src={Logo} alt="Okto" />
                            </Link>
                            <a className="customButtonLink" href="https://oktosquid.gitbook.io/okto/" target="_blank" rel="noreferrer">
                                <div className="customButton" >
                                    <div className="customButtonContainer"><span>Whitepaper</span></div>
                                </div>
                            </a>
                            <Link 
                                className={(location.pathname == '/spawn') ? "customButtonLink active": "customButtonLink"}
                                to="/spawn"
                            >
                                <div className="customButton">
                                    <div className="customButtonContainer"><span>Spawn</span></div>
                                </div>
                            </Link>
                            <Link 
                                className={(location.pathname == '/aquarium') ? "customButtonLink active": "customButtonLink"}
                                to="/aquarium"
                            >
                                <div className="customButton">
                                    <div className="customButtonContainer"><span>Aquarium</span></div>
                                </div>
                            </Link>
                            <Link 
                                className={(location.pathname == '/vault') ? "customButtonLink active": "customButtonLink"}
                                to="/vault"
                            >
                                <div className="customButton">
                                    <div className="customButtonContainer"><span>Vault</span></div>
                                </div>
                            </Link>
                            <ConnectButton 
                                connectionManager={params.connectionManager} 
                                setConnected = {params.setConnected} 
                                connector={params.connector}
                            />
                        </div>
                    </span>
                </div>
            }

            { mobileScreen &&
                <div className="navbar-mobile-header">
                    <div className="pixelBoxed">
                        <span>
                            <div className="pixelBoxedContainer" style={{display: 'flex', justifyContent:'space-between'}}>
                                <Link className="headLogo" to="/" >
                                    <img src={Logo} alt="Okto" />
                                </Link>
                                <div className="customButton" onClick={() => setToggle(!toggleMenu)}>
                                    <div className="customButtonContainer"><span>Menu</span></div>
                                </div>
                            </div>
                        </span>
                    </div>
                </div>
            }

            { toggleMenu &&
                <div className="navbar-mobile">
                    <NavBarMobile 
                        connector={params.connector} 
                        connectionManager={params.connectionManager} 
                        setConnected={params.setConnected}
                        toggle={toggleNavbar}
                    />
                </div>
            }

        </div>
    );
}