import Contract from "../Contract";
import Random from "../Random";
import constants from "../../params.js";

const merkleTree = constants.merkleTree;
const addressToIndex = getAddressToIndex(merkleTree);
console.log("addressToIndex:",addressToIndex);
//Get mapping of index of address leafs by address
function getAddressToIndex(tree) {
    let height = Math.ceil(Math.log2(tree.length));
    let baseSize = Math.pow(2, height-1);
    console.log("tree len:",tree.length,"height:",height,"base:",baseSize);
    let ret = {};
    for(let i = 0; i < baseSize; i++) {
        ret[tree[i].slice(0, 42)] = i;
    }
    return ret;
}
//Get merkle proof for this address
function getProof(idx) {
    let proof = [];
    while(idx < merkleTree.length-1) {
        let sibling = (idx % 2 == 0) ? merkleTree[idx+1] : merkleTree[idx-1];
        proof.push(sibling);
        idx = merkleTree.length - Math.floor((merkleTree.length-idx)/2);
    }
    return proof;
}

export default class Aquarium extends Contract {
    //Actions
    //Mint from whitelist
    async mintWhitelist(mintCost, mintNum) {
        console.log("account:",this.connectionManager.account)
        let idx = addressToIndex[this.connectionManager.account];
        if(idx == undefined) throw("Not whitelisted");
        let seed = Random.seed();
        let proof = getProof(idx);
        console.log("idx,seed,proof:",idx,seed,proof);
        for(let i = 0; i < mintNum; i++) {
            await this.writer.send(
                this.writer.methods.mintWhitelist(
                    seed, proof, idx
                ),
                {value: mintCost}
            )
        }
        console.log("minted from contract");
    }
    //Mint from gen 0 after whitelist opened
    async mintGen0(mintCost, mintNum) {
        for(let i = 0; i < mintNum; i++) {
            await this.writer.send(this.writer.methods.mintGen0(Random.seed()), {value: mintCost});
        }
    } 
    //Mint from gens 1-3
    async mintGenX(mintNum) {
        for(let i = 0; i < mintNum; i++) {
            await this.writer.send(this.writer.methods.mintGenX(Random.seed()), {value: constants.rngCost});
        }
    }
    //Stake NFT
    async stake(id) {
        await this.writer.send(this.writer.methods.stakeNFT(id));
    }
    //Claim NFT
    async claim(id) {
        await this.writer.send(this.writer.methods.claimNFT(id));
    }
    //Unstake NFT
    async unstake(id) {
        await this.writer.send(this.writer.methods.unstakeNFT(id, Random.seed()), {value: constants.rngCost});
    }

    //Pure
    async powerLevel(traits) {
        return await this.view.send(this.view.methods.powerLevel(traits));
    }
    //Views
    async oktoCoinAddress() {
        return await this.view.send(this.view.methods.oktoCoinAddress());
    }
    async revenueManagerAddress() {
        return await this.view.send(this.view.methods.revenueManagerAddress());
    }
    async oktoNFTAddress() {
        return await this.view.send(this.view.methods.oktoNFTAddress());
    }
    async dailyMintRate() {
        return await this.view.send(this.view.methods.dailyMintRate());
    }
    async claimTax() {
        return await this.view.send(this.view.methods.claimTax());
    }
    async unstakeRisk() {
        return await this.view.send(this.view.methods.unstakeRisk());
    }
    async maxOkto() {
        return await this.view.send(this.view.methods.maxOkto());
    }
    async maxSquidPower() {
        return await this.view.send(this.view.methods.maxSquidPower());
    }
    //Cost of minting from gen 0 (in FTM)
    async mintCost0() {
        return await this.view.send(this.view.methods.mintCost());
    }
    //Cost of minting from gen 1-3 (in okto)
    async mintCostX() {
        return await this.view.send(this.view.methods.oktoMintCost());
    }
    //Whether mint is open to all or whitelist only
    async openMint() {
        return await this.view.send(this.view.methods.openMint());
    }
    //Total okto earned
    async totalOktoEarned() {
        return await this.view.send(this.view.methods.totalOktoEarned());
    }
    //Get info from stake
    async stakes(id) {
        return await this.view.send(this.view.methods.stakes(id));
    }
    //Okto earned per stake power
    async oktoEarned() {
        return await this.view.send(this.view.methods.oktoEarned());
    }
    //Okto stolen per squid power
    async oktoStolen() {
        return await this.view.send(this.view.methods.oktoStolen());
    }
    //Time of last claim
    async lastClaimTimestamp() {
        return await this.view.send(this.view.methods.lastClaimTimestamp());
    }
}