import Contract from "../Contract.jsx";
import Web3 from "web3";

export default class Vault extends Contract {
    //Actions:
    //Deposit Okto
    async deposit(amount) {
        await this.writer.send(this.writer.methods.depositOKT(amount));
    }
    //Withdraw Okto and claim FTM rewards
    async withdraw(amount) {
        await this.writer.send(this.writer.methods.withdrawOKT(amount));
    }
    //Claim FTM rewards
    async claim() {
        await this.writer.send(this.writer.methods.claimFTM());
    }

    //Views:
    //See accumulated rewards of address
    async rewards(address) {
        return new Web3.utils.BN(await this.view.send(this.view.methods.rewardsFTM(address)));
    }
    //Total FTM in vault
    async backing() {
        return await this.view.send(this.view.methods.backing());
    }
    //Amount to pay out per staked okto ether per day
    async dailyRewards() {
        let payoutTime = new Web3.utils.BN(await this.view.send(this.view.methods.payoutTime()));
        let timeScale = new Web3.utils.BN(60*60*24);
        return new Web3.utils.BN("10000000000000000000000").mul(timeScale).div(payoutTime);
    }
    //Total supply of vault okto
    async deposits() {
        return await this.view.send(this.view.methods.totalSupply());
    }
    //Deposited balance of account
    async balanceOf(address) {
        return await this.view.send(this.view.methods.balanceOf(address));
    }
}