
//Used for calling view functions of contract
export default class View {
    constructor(abi, address, connectionManager) {
        this.abi = abi;
        this.address = address;
        this.connectionManager = connectionManager;
        this.methods = this.connectionManager.views(this.abi, this.address);
    }
    async send(call, args) {
        return await this.connectionManager.call(call, args);
    } 
}