import constants from "../params.js";
import Web3 from "web3";

//Manage connection to chain via walletconnect or metamask
export default class ConnectionManager {
    //Construct
    constructor() {
        this.connector = undefined;
        this.viewProvider = new Web3(constants.rpcUrl);
        this.connected = false;
        this.contracts = [];
    }
    //Add contract to contracts list for init
    addContract(contract) {
        console.log("added contract",contract);
        this.contracts.push(contract);
    }
    //Initialize the connection manager. Pass a connector object to use walletconnect
    async init(connector) {
        console.log("manager init");
        connector == undefined ? await this.metaInit() : await this.walletInit(connector);
        this.connected = true;
        this.account = this.accounts[0];
        for(let i = 0; i < this.contracts.length; i++) {
            this.contracts[i].initWriter();
        }
    }
    //Initialize with injected web3 provider
    async metaInit() {
        try {
            let ethereum = window.ethereum;
            this.accounts = await ethereum.request({method: 'eth_requestAccounts'});
            const chainId = await ethereum.request({ method: 'eth_chainId' });
            if(chainId != constants.chainId) {
                let success = await ethereum.request({method:"wallet_switchEthereumChain", params:[{chainId:constants.chainId}]});
                console.log("switch",success);
            }
            ethereum.on('accountsChanged', (accounts) => {
                window.location.reload();
            });
            ethereum.on('chainChanged', (chainId) => {
                window.location.reload();
            });
            window.web3 = new Web3(window.ethereum);
            this.web3 = new Web3(Web3.givenProvider);
        } catch(e) {
            console.error(e);
            //await this.metaInit();
        }
    }
    //Initialize with walletconnect
    async walletInit(connector) {
        try {
            this.connector = connector;
            if(!connector.connected) await this.connectConnector(connector);
            this.accounts = connector.accounts;
            this.web3 = new Web3();
        } catch(e) {
            await walletInit(connector);
        }
    }
    //Connect the walletconnect connector
    async connectConnector(connector) {
        connector.createSession();
        await new Promise(res => {
            connector.on("connect", () => {
                res()
            });
        });
    }
    //Get methods of contract at specified abi/address
    methods(abi, address) {
        return new this.web3.eth.Contract(abi, address).methods;
    }
    //Get methods of contract through rpc provider to allow for viewing
    views(abi, address) {
        return new this.viewProvider.eth.Contract(abi, address).methods;
    }
    //Send transaction
    async send(call, args) {
        if(args == undefined) args = {};
        if(args.from == undefined) args.from = this.accounts[0];
        if(args.gasLimit == undefined) args.gasLimit = constants.gasLimit;
        console.log("suggested limit:",args.gasLimit);
        await (this.connector != undefined ? this.walletSend(call, args) : this.metaSend(call, args)).catch((e) => {
            console.error(e);
        });
    }
    //Send transaction with metamask
    async metaSend(call, args) {
        await new Promise((res, rej) => {
            call.send(args).on("error", (err, receipt) => {
                console.log("rej");
                rej();
            }).then(() => {
                console.log("res");
                res();
            });
        });
        console.log("resolved");
    }
    //Send transaction with walletconnect
    async walletSend(call, args) {
        if(args.value == undefined) args.value = 0;
        args.data = call.encodeABI();
        let tx = await this.connector.sendTransaction(args);
        await new Promise(res => {
            this.viewProvider.once(tx, () => {
                res();
            });
        });
    }
    //Call function with rpcUrl
    async call(call, args) {
        try {
            return await call.call(args);
        } catch(e) {
            console.error(e);
            return null;
        }
    }
}