import * as ethers from "ethers";

//Convert string representation in wei to ether by adding a decimal point
function decimalize(num, decimals) {
    if(decimals == 0) {
        console.log("num:",num);
        let ret = num.slice(0, num.length-18);
        if(ret == "") return "0";
        return ret;
    }
    if(decimals == undefined) decimals = 3;
    if(num.length < 18-decimals) {
        let ret = "0.";
        for(let i = 0; i < decimals; i++) ret += "0";
        return ret;
    }
    if(num.length <= 18) {
        let ret = "0.";
        for(let i = 0; i < 18-num.length; i++) ret += "0";
        return (ret+num).slice(0, decimals+2);
    }
    return num.slice(0, num.length-18) + "." + num.slice(num.length-18, num.length-(18-decimals));
} 
//infinity
const oo = ethers.BigNumber.from("2").pow(256).sub(1);

export default {
    decimalize,
    oo
}