//Button for connecting wallet
export default function ConnectButton(params) {
    //Connect to wallet
    async function connect() {
        await params.connectionManager.init(params.connector);
        params.setConnected();
    }

    let buttonText = "Connect Wallet";
    if(params.connectionManager == undefined) buttonText = "Loading";
    else if(params.connectionManager.connected) buttonText = "Connected";
    return (
        <a
            className={(params.connectionManager == undefined || params.connectionManager.connected) ? "walletConnect connected": "walletConnect"}
            onClick={connect} 
        >
            {buttonText}
        </a>
    );
}