import Fade from 'react-fade-in';
import MintButton from "../components/MintButton";
import React from "react";
import mintImage from "../../assets/image/mint_image.png";
import {ethers} from "ethers";

import constants from "../params";
import utils from "../utils";

import './spawn.css';



//TODO: Listen for mint events
export default function SpawnPage(params) {
    let [lastState, setState] = React.useState({
        prevMint: undefined,
        currentGen: undefined,
        totalMints: -1,
        openMint: undefined,
        mintNum: 1
    });
    let state = {
        prevMint: lastState.prevMint,
        currentGen: lastState.currentGen,
        totalMints: lastState.totalMints,
        openMint: lastState.openMint,
        mintNum: lastState.mintNum
    }
    React.useEffect(() => {
        initContracts();
        console.log("use effect");
    }, []);
    async function initContracts() {
        console.log("callback")
        state.currentGen = await params.oktoNFT.currentGen();
        state.totalMints = await params.oktoNFT.totalSupply();
        state.openMint = await params.aquarium.openMint();
        console.log("settings state:",state)
        setState(state);
    }
    console.log("reload",state);

    function currentGen() {
        for(let i = 0; i < 4; i++) {
            if(state.totalMints < constants.genMintCaps[i]) return ""+i;
        }
        return "-";
    }
    function mintMore() {
        if(state.mintNum >= 10) return;
        state.mintNum++;
        setState(state);
    }
    function mintLess() {
        if(state.mintNum <= 1) return;
        state.mintNum--;
        setState(state);
    }
    function mintPercent() {
        return (state.totalMints / 275)+"%";
    }

    return (
        <Fade>
            <div className="spawn-app">   
                <p className='testnet'>Warning: public test on FTM TESTNET only!</p>
                <div className="spawn-container">
                    <div className="pixelBoxed">
                        <span>
                            <div className="pixelBoxedTentacles1">
                                <div className="pixelBoxedTentacles2">
                                    <div className="pixelBoxedContainer">

                                        <p className="text-title"> MINT </p>
                                        <p className="text-description"> Your valuable tokens may now be spent to recruit new Gen {currentGen()} Characters </p> {/* Change X by the variable number of nft gen */}
                                    
                                        <div className="spawn">

                                            <div className="spawn-mint">
                                                <p className="spawn-mint-text"> {state.totalMints} / 27,500 minted</p> {/* Variable number of nft minted */}
                                            </div>

                                            <div className="spawn-mint-container" style={{marginBottom: '3vw'}}>
                                                <p className="spawn-mint-container-text" style={{width: '18%'}}> GEN 0 </p>
                                                <p className="spawn-mint-container-text" style={{width: '36%'}}> GEN 1 </p>
                                                <p className="spawn-mint-container-text" style={{width: '27%'}}> GEN 2 </p>
                                                <p className="spawn-mint-container-text" style={{width: '20%', border: 'none'}}> GEN 3 </p>
                                                <div className="spawn-mint-container-progress" style={{width: mintPercent()}} />  {/* Variable number of nft minted in percent */}
                                            </div>

                                            <div className="spawn-mint">
                                                <p className="spawn-mint-sign noselect" onClick={mintLess}> - </p>
                                                <p className="spawn-mint-text purple number" > {state.mintNum} </p> {/* Variable number of nft minted */}
                                                <p className="spawn-mint-sign noselect" onClick={mintMore}> + </p>
                                            </div>

                                            <div className="spawn-mint" style={{margin: 0, padding: 0}}>
                                                <MintButton 
                                                    oktoNFT={params.oktoNFT} 
                                                    aquarium={params.aquarium}
                                                    setConnected={params.setConnected}
                                                    mintNum={state.mintNum}
                                                    afterMint={initContracts}
                                                    connector={params.connector}
                                                />
                                            </div>

                                            <div className="spawn-mint">
                                                <p className="spawn-mint-text purple"> Price: </p>
                                                <MintCostIndicator totalMints={state.totalMints} mintNum={state.mintNum}/>
                                            </div>

                                        </div>

                                        <div className="spawn-mint">
                                            <p className="spawn-mint-text-end"> Non-Gen 0 Characters may only be minted with $FOOD! </p>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </span>
                    </div>
                </div>
            </div>
        </Fade>
    );
}

function MintCostIndicator(params) {
    let gen0 = params.totalMints < constants.genMintCaps[0];
    let cost = gen0 ? constants.mintCost : constants.oktoMintCost;
    let currency = gen0 ? "FTM" : "$OKT";
    let mintCostString = utils.decimalize(ethers.BigNumber.from(cost).mul(params.mintNum).toString(), 1) + " " + currency;
    return <p className="spawn-mint-text"> {mintCostString} </p>;
}