import Contract from "../Contract.jsx";

export default class OktoCoin extends Contract {
    //Approve spending of token by a contract
    async approve(spender, amount) {
        return await this.writer.send(this.writer.methods.approve(spender, amount));
    }
    //get allowance of address
    async allowance(sender, spender) {
        return await this.view.send(this.view.methods.allowance(sender, spender));
    }
    //get balance of user
    async balanceOf(address) {
        return await this.view.send(this.view.methods.balanceOf(address));
    }
}