import Contract from "../Contract.jsx";

export default class OktoNFT extends Contract {
    //Get traits of an octo
    async getTraits(id) {
        return await this.view.send(this.view.methods.getTraits(id));
    }
    //Current generation minting
    async currentGen() {
        return await this.view.send(this.view.methods.currentGen());
    }
    //Num reminaing mints this generation
    async remainingToMint() {
        return await this.view.send(this.view.methods.remainingToMint());
    }
    //Balance of address
    async balanceOf(address) {
        return await this.view.send(this.view.methods.balanceOf(address));
    }
    //Get specific token of owner
    async tokenOfOwnerByIndex(owner, index) {
        return await this.view.send(this.view.methods.tokenOfOwnerByIndex(owner, index));
    }
    //Get URI of token
    async tokenURI(id) {
        return await this.view.send(this.view.methods.tokenURI(id));
    }
    //Total number minted
    async totalSupply() {
        return await this.view.send(this.view.methods.totalSupply());
    }
}